<template>
  <div class="col s12 m6">
    <div>
      <div class="page-subtitle">
        <h4>{{'Category_Edit' | localize}}</h4>
      </div>

      <form @submit.prevent="submitHandler">
        <div class="input-field">
          <select
            ref="select"
            v-model="current"
          >
            <option
              v-for="c of categories"
              :key="c.id"
              :value="c.id"
            >{{c.title}}</option>
          </select>
          <label>{{'Category_SelectCategory' | localize}}</label>
        </div>

        <div class="input-field">
          <input
            id="name"
            type="text"
            v-model="title"
            :class="{invalid: validateTitle}"
          >
          <label for="name">{{'Category_Name' | localize}}</label>
          <span
            v-if="$v.title.$error"
            class="helper-text invalid"
          >{{'Category_EnterCategoryName' | localize}}</span>
        </div>

        <div class="input-field">
          <input
            id="limit"
            type="number"
            v-model.number="limit"
            :class="{invalid: $v.limit.$dirty && !$v.limit.minValue}"
          >
          <label for="limit">{{'Category_limit' | localize}}</label>
          <span
            v-if="$v.limit.$error"
            class="helper-text invalid"
          >{{'Category_minimalAmount' | localize}} {{$v.limit.$params.minValue.min}} </span>
        </div>

        <button
          class="btn waves-effect waves-light"
          type="submit"
        >
          {{'Update' | localize}}
          <i class="material-icons right">send</i>
        </button>
      </form>
    </div>
  </div>
</template>
<script>
import { required, minValue } from 'vuelidate/lib/validators'
export default {
  props: {
    categories: {
      type: Array,
      required: true
    }
  },
  data: () => ({
    select: null,
    title: '',
    limit: 100,
    current: null
  }),
  validations: {
    title: { required },
    limit: { minValue: minValue(100) }
  },
  computed: {
    validateTitle () {
      return this.$v.title.$dirty && !this.$v.title.required
    }
  },
  methods: {
    async submitHandler () {
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }
      try {
        const categoryData = {
          id: this.current,
          title: this.title,
          limit: this.limit
        }
        await this.$store.dispatch('updateCategory', categoryData)
        this.$message(`Категория ${this.title} обновлена`)
        this.$emit('updated', categoryData)
      } catch (e) { }
    }
  },
  created () {
    const { id, title, limit } = this.categories[0]
    this.current = id
    this.title = title
    this.limit = limit
  },
  watch: {
    current (caId) {
      const { title, limit } = this.categories.find(c => c.id === caId)
      this.title = title
      this.limit = limit
    }
  },
  mounted () {
    this.select = M.FormSelect.init(this.$refs.select) //eslint-disable-line
    M.updateTextFields() //eslint-disable-line
  },
  destroyed () {
    if (this.select && this.select.destroy) {
      this.select.destroy()
    }
  }
}
</script>
